.container {
    width: 50%;
    margin: 60px auto;
    display: flex;
    flex-direction: column;
    text-align: center;
    background: #6a3093; /* fallback for old browsers */
    background: -webkit-linear-gradient(
            to right,
            #a044ff,
            #6a3093
    ); /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(
            to right,
            #a044ff,
            #6a3093
    ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

    min-height: 90vh;
    border-radius: 10px;
}

.todo-form {
    text-align: center;
    margin-top: 30px;
    font-size: 25px;
    display: flex;
    justify-content: center;
}

.todo-input {
    padding: 14px 32px 14px 16px;
    border-radius: 4px;
    border: 2px solid #6a3093;
    outline: none;
    width: 320px;
    background: transparent;
    color: #fff;
    font-size: 25px;
    margin-right: 15px;
}

.todo-input-edit {
    border-radius: 4px;
    border: 2px solid #6a3093;
    outline: none;
    width: 320px;
    background: transparent;
    color: #fff;
    font-size: 25px;
    margin-right: 15px;
}

.todo-input::placeholder {
    color: #e2e2e2;
}

.todo-button {
    border: none;
    border-radius: 4px;
    font-size: 21px;
    width: 70px;
    cursor: pointer;
    outline: none;
    background: #6a3093;
    color: #fff;
    text-transform: capitalize;
}

.todo-list {
    margin-top: 20px;
}

.todo-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 4px auto;
    color: #fff;
    background: #6a3093;
    padding: 16px;
    border-radius: 5px;
    width: 50%;
}

.todo-icon {
    width: 23px;
    height: 23px;
}

.completed {
    text-decoration: line-through;
    opacity: 0.4;
}